@media screen and (min-width: 320px) and (max-width: 767px) {
  .movie-card__item {
    position: relative;
    list-style: none;
    margin-top: 30px;
    cursor: pointer;
    border-radius: 15px;
    transition: box-shadow;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
      box-shadow: 0px 0px 25px 5px rgb(255 107 1 / 70%);
      transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
      transform: scale(1.03);
    }

    &:active {
      transform: scale(1.01);
    }
  }

  .movie-card__image {
    width: 100%;
    height: auto;
    border-radius: 15px;
  }

  .info {
    margin-bottom: 10px;
  }

  .info__item {
    width: 200px;
    font-size: 20px;
    text-transform: uppercase;
    color: getColor('title');
    font-weight: 500;
    padding: 15px 5px 5px 5px;
  }

  .info__about-item {
    font-size: 16px;
    color: getColor('accent');
    font-weight: 500;
    text-align: center;
    padding: 0px 5px 20px 5px;
    display: inline;
    position: relative;

    &:nth-child(3)::before {
      content: '';
      position: absolute;
      width: 40%;
      height: 1px;
      background-color: getColor('accent');
      bottom: 28px;
      left: -10px;
      transform: rotate(90deg);
    }

    &--color-reverse {
      background-color: getColor('accent');
      color: getColor('header-text-color');
      border-radius: 0.4rem;
      padding: 2px 9px;
      margin-left: 4px;
    }
  }
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .movie-card__item {
    position: relative;
    width: 294px;
    list-style: none;
    display: inline-block;
    cursor: pointer;
    margin-right: 30px;
    margin-top: 30px;
    border-radius: 15px;
    flex-basis: calc(100% / 2 - 30px);

    &:nth-of-type(2n) {
      margin-right: -30px;
    }

    transition: box-shadow;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
      box-shadow: 0px 0px 25px 5px rgb(255 107 1 / 70%);
      transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
      transform: scale(1.03);
    }

    &:active {
      transform: scale(1.01);
    }
  }

  .movie-card__image {
    width: 294px;
    height: 398px;
    display: flex;
    border-radius: 15px;
  }

  .info {
    margin-bottom: 10px;
  }

  .info__item {
    font-size: 14px;
    text-transform: uppercase;
    color: getColor('title');
    font-weight: 500;
    padding: 15px 3px 5px 3px;
  }

  .info__about-item {
    font-size: 12px;
    color: getColor('accent');
    width: 293px;
    font-weight: 500;
    text-align: center;
    padding: 0px 5px 15px 5px;
    display: inline;
    position: relative;
    line-height: 1.7;

    &:nth-child(3)::before {
      content: '';
      position: absolute;
      width: 40%;
      height: 1px;
      background-color: getColor('accent');
      bottom: 22px;
      left: -8px;
      transform: rotate(90deg);
    }

    &--color-reverse {
      background-color: getColor('accent');
      color: getColor('header-text-color');
      border-radius: 0.4rem;
      padding: 2px 9px;
      margin-left: 4px;
    }
  }
}

