.backdrop-container-team {
  position: fixed;
  backdrop-filter: blur(5px);
  top: 0;
  left: 0;
  bottom: 0;

  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.516);

  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &.is-hidden {
    //opacity: 0;
    display: none;
    pointer-events: none;
  }
  &__content-team {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #363e57;
    padding: 40px 30px;
    font-size: 12px;
    line-height: calc(16 / 12);
    overflow: scroll;
    height: 584px;
    border-radius: 8px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

    background-image: #808000;

    @media screen and (min-width: 414px) {
      width: 320px;
      overflow: auto;
    }
    @media screen and (min-width: 768px) {
      width: 618px;
      height: 590px;
      padding: 40px 30px;
      top: 50%;
      left: 50%;
      overflow: auto;
    }
    @media screen and (min-width: 1020px) {
      width: 882px;
      height: 675px;
    }
  }
}

.team-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  @media screen and (min-width: 768px) {
    gap: 30px;
  }
}
.team-list__item {
  &:not(:last-child) {
    margin-bottom: 15px;
    @media screen and (min-width: 768px) {
      margin-bottom: 0;
    }
  }
}
.team-img {
  width: 150px;
  height: 200px;
  overflow: hidden;
  border-radius: 15px;
  object-fit: cover;
  transition-property: background-color, color, box-shadow, transform;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  &:hover {
    transform: scale(1.1);
  }
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  @media screen and (min-width: 768px) {
    width: 150px;
    height: 200px;
  }
  @media screen and (min-width: 1020px) {
    width: 150px;
    height: 200px;
    overflow: hidden;
  }
}

.team-list__card {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #078bb3;
  border-radius: 10px;
  padding: 5px 0;
  margin-top: 15px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
}

.team-list__title-developer {
  font-size: 12px;
  line-height: 1.3;
  letter-spacing: 0.5px;
}
.team-list__position-developer {
  font-size: 10px;
  line-height: 1.3;
  letter-spacing: 1px;
  color: #ffffff;
}
.modal-content__close-btn-team {
  position: absolute;
  right: 20px;
  top: 10px;
  padding: 0;
  border: none;
  background-color: transparent;
  color: #000000;
  cursor: pointer;
}
.modal-content__icon-close-team {
  stroke: #000000;
  width: 25px;
  height: 25px;
  transition-property: background-color, color, box-shadow;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    stroke: getColor('accent');
  }
}
.icon-github {
  width: 25px;
  height: 25px;
  fill: #ffffff;
  transition-property: background-color, color, box-shadow;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    fill: #000000;
  }
}

.git-link {
  display: block;
}
.team-info {
  margin-right: 10px;
  margin-bottom: 10px;
}
